<template>
    <div>
        <section class="faq-section padding-section" id="faq">
            <div class="container">
                <div class="row">
                    <div class="col-md-12">
                        <div class="faq-accordion-section">
                            <div class="demo">
                                <div class="accordion accordion-flush" id="accordionExample">
                                    <div v-for="(faq, index) in faqs" :key="index = 1" class="accordion-item card"
                                        data-aos="fade-up">
                                        <div class="card-header" :id="'heading' + faq.id">
                                            <h2 class="mb-0">
                                                <button type="button" class="btn btn-link accordion-button collapsed"
                                                    data-bs-toggle="collapse" :data-bs-target="'#collapse' + faq.id"
                                                    aria-expanded="false" :aria-controls="'heading' + faq.id"> {{ faq.question
                                                    }} </button>
                                            </h2>
                                        </div>
                                        <div :id="'collapse' + faq.id" class="accordion-collapse collapse"
                                            aria-labelledby="flush-headingOne" data-bs-parent="#accordionExample">
                                            <div class="card-body">
                                                <p>{{ faq.answer }}</p>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>


                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
import { onMounted } from "vue";
import axios from 'axios';
export default {
    data() {
        return {
            faqs: [],
        }
    },
    mounted() {
        axios.get("http://paizattoapi.paizatto.com/api/admin/faq").then((response) => {
            if (response.data.success == true) {
                this.faqs = response.data.data;
            }
        });
    },
    methods: {},
    setup() {
        // UnRegister on leave
        onMounted(() => {
            console.log("demo");
        });

        return {};
    },
}
</script>

<style></style>