<template>
    <div>
        <div class="padding-section contact-us-section" id="contact-us">
            <div class="container">
                <div class="row align-items-center justify-content-center">
                    <div class="col-sm-12 col-md-12 col-lg-12">
                        <div class="title-section mb-5" data-aos="flip-left">
                            <h3>Contact Us</h3>
                        </div>
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-6">
                        <div class="contact-form-section" data-aos="fade-up">

                            <form class="contact-form" @submit.prevent="contactformSubmit">

                                <div class="form-group">
                                    <label>Full Name <span class="text-danger">*</span></label>
                                    <input type="text" class="form-control required" name="name" v-model="name">
                                    <span class="text-danger" v-show="isShow">This field is
                                        required</span>
                                </div>

                                <div class="form-group mobile-no-error">
                                    <label>Mobile Number <span class="text-danger">*</span></label>
                                    <div class="input-group">
                                        <input type="number" class="form-control required" v-model="mobile"
                                            :maxLength="maxLength" :disabled="disabled1">
                                        <span class="text-danger mobile-no-errortext" v-show="isShow">This field
                                            is required</span>
                                        <div class="input-group-prepend">
                                            <button class="btn btn-outline-secondary otp-btn" type="button"
                                                @click="sendOTP">Send OTP</button>
                                        </div>
                                    </div>
                                </div>
                                <div class="form-group" v-show="showOtpInput">
                                    <label>Enter OTP <span class="text-danger">*</span></label>
                                    <input type="number" class="form-control required" v-model="otp">
                                    <span class="text-danger error-span">This field is required</span>
                                </div>
                                <div class="form-group">
                                    <label>Email Address</label>
                                    <input type="email" class="form-control" v-model="email" :disabled="disabled">
                                </div>
                                <div class="form-group">
                                    <label>Message <span class="text-danger">*</span></label>
                                    <textarea rows="4" class="form-control required" v-model="message"
                                        :disabled="disabled"></textarea>
                                    <span class="text-danger error-span">This field is required</span>
                                </div>
                                <div class="text-center mt-4">
                                    <button type="submit" class="btn btn-common">Submit</button>
                                </div>
                            </form>

                        </div>
                    </div>
                </div>
                <div class="row align-items-center">
                    <div class="col-sm-12 col-md-6 col-lg-6">
                        <div class="contact-info-section">
                            <div class="row align-items-center">
                                <div class="col-3 col-sm-12 col-md-4 col-lg-4">
                                    <img src="../assets/img/phone.png">
                                </div>
                                <div class="col-9 col-sm-12 col-md-8 col-lg-8">
                                    <h4>Call Us</h4>
                                    <h3>9007 9007 80</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-sm-12 col-md-6 col-lg-6">
                        <div class="contact-info-section pb-sm-20">
                            <div class="row align-items-center">
                                <div class="col-3 col-sm-12 col-md-4 col-lg-4">
                                    <img src="../assets/img/email.png">
                                </div>
                                <div class="col-9 col-sm-12 col-md-8 col-lg-8">
                                    <h4>Email Us</h4>
                                    <h3>info@paizatto.com</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import { onMounted } from "vue";
export default {
    conponents: {

    },
    data() {
        return {
            name: '',
            email: '',
            mobile: '',
            otp: '',
            message: '',
            isShow: false,
            showOtpInput: false,
            disabled: true,
            disabled1: false,
            maxLength: 10,
        }
    },
    methods: {
        sendOTP() {
            console.log('test');
            if (this.name !== '' && this.mobile !== '') {
                if (this.mobile.toString().length == 10) {
                    const data = { phoneNumber: this.mobile };
                    fetch("http://paizattoapi.paizatto.com/api/admins/sendOTPCodeAssociatenew", {
                        method: "POST", // or 'PUT'
                        headers: {
                            "Content-Type": "application/json",
                        },
                        body: JSON.stringify(data),
                    })
                        .then((response) => response.json())
                        .then((data) => {

                            if (data.code === 200) {
                                this.showOtpInput = true;
                                this.disabled = false;
                                this.disabled1 = true;
                                localStorage.setItem('SetOtp', data.otp);
                                this.$swal("Success", data.status, "success");
                            } else {
                                this.$swal("Error", data.message, "error");
                            }
                        })
                        .catch((error) => {
                            this.isLoad = !this.isLoad;
                            console.error("Error:", error);
                        });
                } else {
                    this.$swal("Error", "Please enter valid phone number", "error");
                }
            } else {
                this.isShow = true;
                // this.errorshow = true;
                this.$swal("Error", "Please fill the required fields", "error");
            }
        },

        contactformSubmit() {
            var verifyOTP = localStorage.getItem('SetOtp');
            console.log(verifyOTP);
            // debugger;
            if (verifyOTP == this.otp) {
                const data = { name: this.name, mobile: this.mobile, email: this.email, message: this.message };
                fetch("http://paizattoapi.paizatto.com/api/users/contactus", {
                    method: "POST", // or 'PUT'
                    headers: { "Content-Type": "application/json", },
                    body: JSON.stringify(data),
                })
                    .then((response) => response.json())
                    .then((data) => {
                        // this.isLoad = !this.isLoad;
                        if (data.code === 200) {
                            this.$swal("Success", data.status, "success");
                            setTimeout(function () {
                                location.reload();
                            }, 2000);
                        } else {
                            this.$swal("Error", data.status, "error");
                        }
                    })
                    .catch((error) => {
                        // this.isLoad = !this.isLoad;
                        console.error("Error:", error);
                    });
            } else {
                this.$swal("Incorrect OTP", "Please enter valid OTP", "error");
            }
        },
    },
    setup() {
        // UnRegister on leave
        onMounted(() => {
            console.log("demo");
        });

        return {};
    },
}
</script>

<style scoped>
.form-group {
    margin-bottom: 15px;
}
</style>