import { createRouter, createWebHistory } from 'vue-router'
import Home from '../components/homePage.vue'
import Contact from '../components/contactPage.vue'
import Faq from '../components/faqPage.vue'
import Register from '../components/registerPage.vue'
import Privacypolicy from '../components/privacypolicyPage.vue'
import Terms from '../components/termsPage.vue'
import Newsmedia from '../components/newsmediaPage.vue'
import Video from '../components/videoPage.vue'
import News from '../components/newsPage.vue'

const router = createRouter({
    history: createWebHistory(),
    routes: [{
            path: '/',
            name: 'homePage',
            include: 'headerView',
            component: Home,
        },
        {
            path: '/contact-us',
            name: 'contactPage',
            include: 'headerView',
            component: Contact,
        },
        {
            path: '/faq',
            name: 'faqPage',
            include: 'headerView',
            component: Faq,
        },
        {
            path: '/register',
            name: 'registerPage',
            include: 'headerView',
            component: Register,
        },
        {
            path: '/privacy-policy',
            name: 'privacypolicyPage',
            include: 'headerView',
            component: Privacypolicy,
        },
        {
            path: '/terms-condition',
            name: 'termsPage',
            include: 'headerView',
            component: Terms,
        },
        {
            path: '/news-room',
            name: 'newsmediaPage',
            include: 'headerView',
            component: Newsmedia,
        },
        {
            path: '/videos',
            name: 'videoPage',
            include: 'headerView',
            component: Video,
        },
        {
            path: '/news',
            name: 'newsPage',
            include: 'headerView',
            component: News,
        },
    ]
})

export default router