<template>
    <div>
        <div class="associate-register-bg-section">
            <div class="container">
                <!-- <div class="logo-position">
                    <img src="../assets/img/logo.png" alt="logo" class="logo-section">
                </div> -->
                <div class="row align-items-center justify-content-center">
                    <div class="col-12 col-md-12 col-lg-3 col-xl-3">
                        <div class="logo-position">
                            <img src="../assets/img/logo.png" alt="logo" class="logo-section">
                        </div>
                    </div>
                    <div class="col-12 col-md-12 col-lg-6 col-xl-6">
                        <div class="associate-register-form-section">
                            <div class="card-sec">
                                <form @submit.prevent="submitForm()">
                                    <div class="form-name-sec text-center">
                                        <h3>Associate Registration</h3>
                                    </div>
                                    <div class="row align-items-center justify-content-center">
                                        <div class="col-md-12 mb-4">
                                            <div class="form-group position-relative">
                                                <input placeholder="* Name" name="associatefirstName"
                                                    v-model="associatefirstName" class="form-control border-0" type="text"
                                                    style="width: 100%" />
                                                <span class="input-icon-position"><i class="fa fa-user-circle-o"
                                                        aria-hidden="true"></i></span>
                                            </div>
                                        </div>

                                        <div class="col-md-12 mb-4">
                                            <div class="form-group position-relative">
                                                <input placeholder="* Mobile No" v-model="phoneNumber"
                                                    class="form-control border-0" type="number" style="width: 100%" />
                                                <span class="input-icon-position"><i class="fa fa-mobile"
                                                        aria-hidden="true"></i></span>
                                            </div>
                                        </div>
                                        <div class="col-md-8 mb-4">
                                            <div class="form-group">
                                                <input placeholder="* OTP" name="associatefirstName" v-model="otp"
                                                    class="form-control border-0" type="number" style="width: 100%" />
                                            </div>
                                        </div>
                                        <div class="col-md-4 mb-4">
                                            <div class="form-group text-right">
                                                <button class="btn send-otp-btn" id="sendOTP" @click.prevent="sendOTP">
                                                    Send OTP
                                                </button>
                                            </div>
                                        </div>
                                        <div class="col-md-12 mb-4">
                                            <div class="form-group text-start w-100">
                                                <input placeholder="EX : A000000001" class="form-control border-0"
                                                    type="text" v-model="getReferralId" :disabled="disabled == 1" />
                                                <p class="text-start w-100 fw-600 f-14 clr-green pt-2 ps-1"
                                                    v-if="this.firstName != null">
                                                    <span class="clr-black">Referrer Name :</span> {{ this.firstName }}
                                                </p>
                                            </div>
                                        </div>
                                        <div class="col-md-12 mb-4">
                                            <div class="form-group">
                                                <div class="form-check text-center p-0">
                                                    <input class="form-check-input float-none mr-1" type="checkbox" checked
                                                        value="1" v-model="isAccept" />
                                                    <label class="form-check-label ps-1" for="invalidCheck2">
                                                        <strong>
                                                            <small>* By clicking Submit, you agree to our
                                                                <a target="_blank" href="/terms-condition" class="a-link">
                                                                    Terms &amp; Conditions.</a>
                                                            </small>
                                                        </strong>
                                                    </label>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-md-12 mb-4">
                                            <div class="text-center">
                                                <button id="submitbtn" type="submit" class="btn btn-show-more mx-3">
                                                    Submit
                                                </button>
                                                <a href="/" type="button" class="btn btn-outline-secondary">Home</a>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-3 position-relative">
                        <div class="shopping-img-sec">
                            <img src="../assets/img/shopping-img1.png" alt="logo" class="">
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { onMounted } from "vue";

// import axios from "axios";
export default {
    data() {
        return {
            associatefirstName: null,
            disabled: 0,
            isAccept: true,
            isLoad: false,
            phoneNumber: null,
            otp: null,
            firstName: null,
            referrID: null,
            getReferralId: null,
            name: "Associate Registration",
            isverify: false,
            count: 0,
            props: {
                msg: String,
            },
        };
    },

    mounted() {
        const urlParams = new URLSearchParams(window.location.search);
        this.referrID = urlParams.get("referralid");
        if (this.referrID) {
            const dataInfo = { ReferralId: this.referrID };
            fetch("https://paizattowebapi.paizatto.com/api/users/validateReferralUID", {
                method: "POST", // or 'PUT'
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(dataInfo),
            })
                .then((response) => response.json())
                .then((data) => {
                    if (data.code === 200) {
                        this.getReferralId = data.data.uid;
                        this.firstName = data.data.firstName;
                        this.disabled = 1;
                        this.isverify = true;
                        this.$swal("Success", "Referral Id Verified", "success");
                    } else {
                        this.isverify = false;
                        this.$swal("Error", "Incorrect Referral Id", "error");
                    }
                })
                .catch((error) => {
                    this.$swal("Oops!", error, "error");
                });
        }
    },

    watch: {
        getReferralId: function (val, oldval) {
            if (val.length == 10 && this.count > 1) {
                this.validateReferId();
            }
            if (val.length > 10) {
                this.getReferralId = oldval;
            }
            this.count++;
        },

        phoneNumber: function (val, oldval) {
            if (val.toString().length > 10) {
                this.phoneNumber = oldval;
                console.log(val);
            }
        },
    },
    methods: {
        validateReferId() {
            this.firstName = "";
            const dataInfo = { ReferralId: this.getReferralId };
            fetch("https://paizattowebapi.paizatto.com/api/users/validateReferralID", {
                method: "POST", // or 'PUT'
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(dataInfo),
            })
                .then((response) => response.json())
                .then((data) => {
                    if (data.code === 200) {
                        this.firstName = data.data.firstName;
                        this.isverify = true;
                        this.$swal("Success", "Referral Id Verified", "success");
                    } else {
                        this.$swal("Oops!", data.message, "error");
                        this.isverify = false;
                    }
                })
                .catch((error) => {
                    this.$swal("Oops!", error, "error");
                });
        },
        sendOTP() {
            if (this.phoneNumber != null) {
                if (this.phoneNumber.toString().length == 10) {
                    this.isLoad = !this.isLoad;
                    const data = { phoneNumber: this.phoneNumber };
                    fetch("https://paizattowebapi.paizatto.com/api/users/sendotpcode", {
                        method: "POST", // or 'PUT'
                        headers: {
                            "Content-Type": "application/json",
                        },
                        body: JSON.stringify(data),
                    })
                        .then((response) => response.json())
                        .then((data) => {
                            this.isLoad = !this.isLoad;
                            if (data.code === 200) {
                                this.$swal("Success", data.status, "success");
                            } else {
                                this.$swal("Error", data.message, "error");
                            }
                        })
                        .catch((error) => {
                            this.isLoad = !this.isLoad;
                            console.error("Error:", error);
                        });
                } else {
                    this.$swal("Error", "Please Enter 10 Digit Valid Mobile Number", "error");
                }
            } else {
                this.$swal("Error", "Mobile Number Required", "error");
            }
        },
        submitForm() {
            if (this.isAccept) {
                console.log(this.getReferralId);

                if (this.getReferralId === "" || this.getReferralId == null) {
                    this.isverify = true;
                }
                if (this.isverify == true) {
                    this.isverify = false;
                    this.isLoad = !this.isLoad;
                    document.querySelectorAll("#submitbtn").disabled = true;
                    const data = { phoneNumber: this.phoneNumber, otp: this.otp };
                    fetch("https://paizattowebapi.paizatto.com/api/users/verifyotpcode", {
                        method: "POST", // or 'PUT'
                        headers: {
                            "Content-Type": "application/json",
                        },
                        body: JSON.stringify(data),
                    })
                        .then((response) => response.json())
                        .then((data) => {
                            this.isLoad = !this.isLoad;
                            if (data.code === 200) {
                                this.submitForm1();
                            } else {
                                this.$swal("Error:", data.status, "error");
                            }
                        })
                        .catch((error) => {
                            this.isLoad = !this.isLoad;
                            console.error("Error:", error);
                        });
                } else {
                    this.$swal("Error", "Id Not Verified", "error");
                }
            } else {
                this.$swal("Error", "Accept our terms and condition to proceed", "error");
            }
        },

        submitForm1() {
            this.isLoad = !this.isLoad;
            const data = { phoneNumber: this.phoneNumber, firstName: this.associatefirstName };
            if (this.getReferralId) {
                data.ReferralId = this.getReferralId;
            } else {
                data.ReferralId = "";
            }

            fetch("https://paizattowebapi.paizatto.com/api/users/registernew", {
                method: "POST", // or 'PUT'
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(data),
            })
                .then((response) => response.json())
                .then((data) => {
                    this.isLoad = !this.isLoad;
                    if (data.code === 200) {
                        this.$swal("Success", data.message, "success");
                        setTimeout(function () {
                            location.reload();
                        }, 2000);
                    } else {
                        this.$swal("Error", data.message, "error");
                    }
                })
                .catch((error) => {
                    this.isLoad = !this.isLoad;
                    console.error("Error:", error);
                });
        },
    },
    setup() {
        // UnRegister on leave
        onMounted(() => {
            console.log("demo");
        });

        return {};
    },
}
</script>

<style lang="css" scoped></style>